/**
 * Players default data
 * @type {number}
 */
let choosedPlayer = 0;
const playerFaces = [
  {
    name: ' ',
    avatar: 1,
  },
  {
    name: ' ',
    avatar: 2,
  },
  {
    name: ' ',
    avatar: 3,
  },
  {
    name: ' ',
    avatar: 4,
  },
  {
    name: ' ',
    avatar: 5,
  },
  {
    name: ' ',
    avatar: 6,
  },
  {
    name: ' ',
    avatar: 7,
  },
  {
    name: ' ',
    avatar: 8,
  },
  {
    name: ' ',
    avatar: 9,
  },
].sort((a, b) => 0.5 - Math.random());

/**
 * Get random player face
 * @returns {Object}
 */
export default function getRandomFace() {
  return playerFaces[choosedPlayer++ % playerFaces.length];
}
